<template>
  <div class="timerContainer">
    <b-row>
      <b-col cols="4"></b-col>
      <b-col cols="4">
        <h1 id="heading">Task Timer</h1>
      </b-col>
      <b-col cols="4"></b-col>
    </b-row>
    <b-row>
      <b-col></b-col>
      <b-col cols="6">
        <div class="timer-area">
          <div class="timer">
            <span class="minutes">{{minutes}}</span>
            <span>:</span>
            <span class="seconds">{{seconds}}</span>
          </div>
          <div>
            <b-input-group>
              <b-dropdown 
                id="ddProject"
                name="ddProject"
                size="lg"
                class="mb-3"
                v-if="!started && !paused && !prjInputPopulated"
                v-model="ddProjectObj.ddProjectSelectedOption"
                text="Select Project"
                variant="success">
                <b-dropdown-item disabled value="0">Select a Project</b-dropdown-item>
                <b-dropdown-item 
                  v-for="prjOption in ddProjectObj.prjOptions" 
                  :key="prjOption.value" 
                  :value="prjOption.value"
                  @click="ddProjectObj.ddProjectSelectedOption = prjOption.value;setPrjInputFromDropDn()">
                  {{prjOption.text}}
                </b-dropdown-item>           
              </b-dropdown> 
              <b-form-input 
                v-model="projectInput" 
                type="text"
                size="lg"
                placeholder="Enter a new project"
                v-if="!started && !paused && !prjInputPopulated"
                v-on:change="setProject(); prjInputChgd()">
              </b-form-input>
            </b-input-group>
          </div>
          <div>
            <b-input-group>
              <b-dropdown 
                id="ddTask"
                name="ddTask"
                size="lg"
                class="mb-3"
                v-if="!started && !paused && !taskInputPopulated"
                v-model="ddTaskObj.ddTaskSelectedOption"
                text="Select Task"
                variant="success"
                v-on:change="setTask">
                <b-dropdown-item disabled value="0">Select a Task</b-dropdown-item>
                <b-dropdown-item v-for="taskOption in ddTaskObj.taskOptions" 
                  :key="taskOption.value" 
                  :value="taskOption.value"
                  @click="ddTaskObj.ddTaskSelectedOption = taskOption.value; setTaskInputFromDropDn();">
                  {{taskOption.text}}
                </b-dropdown-item>           
              </b-dropdown> 
              <b-form-input 
                class="fm-input"
                v-model="taskInput" 
                type="text"
                size="lg"
                placeholder="Enter a new task"
                v-if="!started && !paused && !taskInputPopulated"
                v-on:change="setTask(); taskInputChgd()">
              </b-form-input>
            </b-input-group>
          </div>
          <p v-if="taskInputPopulated"><i>For the time remaining, focus on completing this task:</i></p>
          <h1 v-if="taskInputPopulated">{{taskInput}}</h1>
          <b-row 
            class="set-timer mb-3 " 
            align-h="center">
            <b-dropdown 
              id="startTimer"
              name="startTimer"
              size="lg"
              v-if="!started && !paused"
              v-model="ddTimerObj.ddTimerSelectedOption"
              text="Set Timer"
              variant="success"
              v-on:change="setTimer">
              <b-dropdown-item disabled value="0">Select a Time Period</b-dropdown-item>
              <b-dropdown-item v-for="option in ddTimerObj.options" 
                :key="option.value" 
                :value="option.value"
                @click="ddTimerObj.ddTimerSelectedOption = option.value; setTimer();">
                {{option.text}}
            </b-dropdown-item>           
            </b-dropdown> 
          </b-row>
          <div class="controls">
            <div class="start mb-3" 
              v-if="!started && !paused" 
              @click="startTimer">
              <b-button 
                size="lg" 
                variant="success" 
                id="start-btn">Start 
              </b-button>
            </div>
            <div class="pause mb-3"  
              v-if="started" 
              @click="stopTimer">
              <b-button 
                size="lg" 
                variant="success" 
                id="pause-btn">Pause
              </b-button>
            </div>
            <div class="resume mb-3" v-if="resume" @click="startTimer">
              <b-button size="lg" variant="success" id="resume-btn" > Resume </b-button>
            </div>
            <div class="reset mb-3" v-if="resetButton" @click="resetTimer">
              <b-button size="lg" variant="success" id="stop-btn">Reset</b-button>
            </div>
          </div>
        </div>
      </b-col>
      <b-col></b-col>
    </b-row>   
  </div>
</template>

<script>
export default {
  name: "TimerPage",
  data() {
    return {
      task: "",
      projectInput: "",
      prjInputPopulated: false,
      taskInput: "",
      taskInputPopulated: false,
      timer: null,
      started: false,
      paused: false,
      resume: false,
      totalTime: 25 * 60,
      resetButton: false,
      ddProjectObj: {
        originalValue: [],
        ddProjectSelectedOption: "",
        disabled: false,
        readonly: false,
        visible: true,
        color: "",
        prjOptions: [],
      },
      ddTaskObj: {
        originalValue: [],
        ddTaskSelectedOption: "",
        disabled: false,
        readonly: false,
        visible: true,
        color: "",
        taskOptions: [],
      },
      ddTimerObj: {
        originalValue: [],
        ddTimerSelectedOption: "25",
        disabled: false,
        readonly: false,
        visible: true,
        color: "",
        options: [
          {
            value: 10,
            text: "10 minutes"
          },
          {
            value: 15,
            text: "15 minutes"
          },
          {
            value: 20,
            text: "20 minutes"
          },
          {
            value: 25,
            text: "25 minutes"
          }
        ]
      }
    };
  },
  methods: {
    playSound: function(sound) {
      if (sound) {
        var audio = new Audio(sound);
        audio.play();
      }
    },
    prjInputChgd: function() {
      console.log("project input has changed");
      //this.prjInputPopulated = true;
    },
    setProject: function() {
      console.log("entering setProject");
      this.prjObj = {
        value: this.projectInput,
        text: this.projectInput
      }
      this.ddProjectObj.prjOptions.push(this.prjObj);
      console.log(JSON.stringify(this.ddProjectObj.prjOptions, null, 2) + "\n");
      console.log("leaving setProject");
    },
    setPrjInputFromDropDn: function() {
      console.log("entering setProjectFromDropDn");
      console.log("selected Dropdown Option " + this.ddProjectObj.ddProjectSelectedOption);
      this.projectInput = this.ddProjectObj.ddProjectSelectedOption;
      console.log("leaving setProjectFromDropDn");
    },
    taskInputChgd: function() {
      console.log("task input has changed");
    //  this.taskInputPopulated = true;
    },
    setTask: function() {
      console.log("entering setTask");
      this.taskObj = {
        value: this.taskInput,
        text: this.taskInput
      }
      this.ddTaskObj.taskOptions.push(this.taskObj);
      console.log(JSON.stringify(this.ddTaskObj.taskOptions, null, 2) + "\n");
      console.log("leaving setTask");
    },
    setTaskInputFromDropDn: function() {
      console.log("entering setTaskFromDropDn");
      console.log("selected Task Dropdown Option " + this.ddTaskObj.ddTaskSelectedOption);
      this.taskInput = this.ddTaskObj.ddTaskSelectedOption;
      console.log("task Input is " + this.taskInput);
      console.log("leaving setTaskFromDropDn");
    },
    setTimer: function() {
      this.totalTime = this.ddTimerObj.ddTimerSelectedOption * 60;
    },
    startTimer: function() {
      this.timer = setInterval(() => this.countdown(), 1000); //1000ms = 1 second
      this.resetButton = false;
      this.paused = false;
      this.resume = false;
      this.started = true;
      this.prjInputPopulated = true;
      this.taskInputPopulated = true;
    },
    stopTimer: function() {
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = true;
      this.paused = true;
      this.resume = true;
      this.started = false;
    },
    resetTimer: function() {
      this.totalTime = this.ddTimerObj.ddTimerSelectedOption * 60;
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = false;
      this.paused = false;
      this.started = false;
      this.resume = false;
      this.prjInputPopulated = false;
      this.projectInput = "";
      this.taskInputPopulated = false;
      this.taskInput = "";
    },
    editTimer: function() {
      this.edit = true;
    },
    padTime: function(time) {
      return (time < 10 ? "0" : "") + time;
    },
    countdown: function() {
      if (this.totalTime === 0) {
        this.started = false;
        this.paused = true;
        this.resetButton = true;
        this.playSound(
          "http://soundbible.com/mp3/Elevator Ding-SoundBible.com-685385892.mp3"
        );
      } else {
        this.totalTime--;
      }
    }
  },
  computed: {
    minutes: function() {
      const minutes = Math.floor(this.totalTime / 60);
      return this.padTime(minutes);
    },
    seconds: function() {
      const seconds = this.totalTime - this.minutes * 60;
      return this.padTime(seconds);
    }
  }
};
</script>

